$react-light-blue: #61dafb;
$primary-main-purple: #3b1cdf;

.utsearch-btns {
  display: flex;
  flex-direction: row;
  margin-bottom: 1em;

  .utsearch-btn {
    margin-right: 0.5rem;

    i {
      font-size: 1.5rem;
    }
  }
}

#utsearch-app-btn {
  &:hover {
    #utsearch-app {
      color: $react-light-blue;
    }
  }
}

#utsearch-youtube-btn {
  border: 1px solid #0000003b;

  &:hover {
    border: 1px solid #a3a2a5;
  }

  #utsearch-youtube {
    color: #a3a2a5;
  }

  &:hover {
    #utsearch-youtube {
      color: red;
    }
  }
}

#utsearch-github-btn {
  border: 1px solid #0000003b;

  &:hover {
    border: 1px solid #a3a2a5;
  }

  #utsearch-github {
    color: #a3a2a5;
  }

  &:hover {
    #utsearch-github {
      color: black;
    }
  }
}

#our-app {
  outline: none;
}
